@import url('https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC');

.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  padding: 10px;
}

/* Set a class for the td element holding the image */
.recipe-photo {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  height: auto; /* or whatever fixed height you prefer */
  overflow-y: auto; /* enables vertical scrolling when needed */
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}

.recipe-row-odd {
  background-color: #ecf7fd;
}

.recipe-row-even {
  background-color: #ffffff;
}

/* Column Size for thead */


/* Column size for tbody */

h1 {
  font-family: 'Playfair Display SC', serif;
  text-align: center;
  font-size: 64px;
}
